const navItemNames = {
    home: 'home',
    cat: 'cat',
    search: 'search',
    option: 'option',
    about: 'about',
};

const navItems = [
    { id: 1, name: navItemNames.home, text: '', icon: 'home' },
    { id: 2, name: navItemNames.cat, text: '', icon: 'tags-o' },
    { id: 3, name: navItemNames.search, text: '', icon: 'search' },
    { id: 4, name: navItemNames.option, text: '', icon: 'frown-o' },
    { id: 5, name: navItemNames.about, text: '', icon: 'ellipsis' },
];

export default navItems;
export { navItemNames };


